 width: 20px;\r\n    height: 20px;\r\n    vertical-align: middle;\r\n    padding-bottom: 2px;\r\n  transition:.5s ease-in-out ;\r\n  -moz-transition: .5s ease-in-out ;\r\n  -webkit-transition: .5s ease-in-out;\r\n}\r\n\r\n.img{\r\n    width: 100%;\r\n    height: auto;\r\n}\r\n\r\n@import './assets/modules/form.scss';\r\n\r\n@import './assets/modules/header.scss';\r\n@import './assets/modules/PrincipalMenuStyle.scss';\r\n@import './assets/modules/topbar.scss';\r\n@import './assets/modules/cart.scss';\r\n\r\n\r\n\r\n@import './assets/modules/banner.scss';\r\n@import './assets/modules/progressbar.scss';\r\n@import './assets/modules/skill.scss';\r\n@import './assets/modules/sectionform.scss';\r\n@import './assets/modules/sectionproject.scss';\r\n\r\n\r\n@import './assets/modules/footer.scss';\r\n\r\n@import './assets/modules/cartAdmin.scss';\r\n@import './assets//modules/adminSign.scss';\r\n@import './assets/modules/adminMenu.scss';\r\n@import './assets//modules/directoryAdmin.scss';\r\n@import './assets/modules/asideAdmin.scss';\r\n@import './assets/modules/projectsAdmin.scss';","// http://meyerweb.com/eric/tools/css/reset/\r\n//  v2.0 | 20110126\r\n//  License: none (public domain)\r\n* {\r\n    box-sizing: border-box;\r\n  }\r\n  html, body, div, span, applet, object, iframe,\r\n  h1, h2, h3, h4, h5, h6, p, blockquote, pre,\r\n  a, abbr, acronym, address, big, cite, code,\r\n  del, dfn, em, img, ins, kbd, q, s, samp,\r\n  small, strike, strong, sub, sup, tt, var,\r\n  // b, u, i, center,\r\n  dl, dt, dd, ol, ul, li,\r\n  fieldset, form, label, legend,\r\n  table, caption, tbody, tfoot, thead, tr, th, td,\r\n  article, aside, canvas, details, embed,\r\n  figure, figcaption, footer, header, hgroup,\r\n  menu, nav, output, ruby, section, summary,\r\n  time, mark, audio, video {\r\n    margin: 0;\r\n    padding: 0;\r\n    border: 0;\r\n    font-size: 100%;\r\n    font: inherit;\r\n    vertical-align: baseline;\r\n  }\r\n  // HTML5 display-role reset for older browsers\r\n  article, aside, details, figcaption, figure,\r\n  footer, header, hgroup, menu, nav, section {\r\n    display: block;\r\n  }\r\n  body {\r\n    line-height: 1;\r\n  }\r\n  ol, ul {\r\n    list-style: none;\r\n  }\r\n  blockquote, q {\r\n    quotes: none;\r\n  }\r\n  blockquote:before, blockquote:after,\r\n  q:before, q:after {\r\n    content: '';\r\n    content: none;\r\n  }\r\n  table {\r\n    border-collapse: collapse;\r\n    border-spacing: 0;\r\n  }\r\na {\r\n    text-decoration: none;\r\n    color: inherit;\r\n    transition:.5s color ease-in-out ;\r\n    -moz-transition: .5s color ease-in-out ;\r\n    -webkit-transition: .5s color ease-in-out;\r\n}\r\n","// Variables\r\n// ===================\r\n\r\n$padding:40px;\r\n%container,\r\n.container{\r\n    @include container(1260px, $padding);\r\n}\r\n\r\n%formInput{\r\n    outline: none;\r\n    border: none;\r\n}\r\n\r\n.active{\r\n    color: var(--colorActive);\r\n}\r\n\r\n%wrap{\r\n    display:flex;\r\n    flex-flow: row wrap;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n}\r\n\r\n.title,\r\n%title{\r\n    font-weight:700;\r\n    font-size: rem(32);\r\n    line-height: 1.4;\r\n}\r\n\r\n%btn{\r\n    padding: space(1) space(2.5);\r\n    transition: .5s;\r\n    cursor:pointer;\r\n    font-size:rem(16);\r\n    border: none;\r\n    border-radius: 5px;\r\n    margin-bottom: space(.5);\r\n}\r\n\r\nimg{\r\n    width: 100%;\r\n}\r\n\r\n.main{\r\n    min-height: 446px;\r\n}","@import \"function\";\r\n\r\n// Responsive\r\n// ==================\r\n@mixin up($size) {\r\n  $size: strip-unit($size);\r\n  @media (min-width: $size * 1px) { @content; }\r\n}\r\n@mixin down($size) {\r\n  $size: strip-unit($size);\r\n  @media (max-width: $size * 1px) { @content; }\r\n}\r\n@mixin between($down, $up) {\r\n  $down: strip-unit($down);\r\n  $up: strip-unit($up);\r\n  @media (min-width: $down * 1px) and (max-width: $up * 1px) { @content; }\r\n}\r\n\r\n// Espacement\r\n// ==================\r\n@mixin container($width, $padding) {\r\n  width: 100%;\r\n  padding-left: $padding/4;\r\n  padding-right: $padding/4;\r\n\r\n  @include up($width/2) {\r\n    padding-left: $padding;\r\n    padding-right: $padding;\r\n  }\r\n\r\n  @include up($width + 2 * $padding) {\r\n    padding-left: calc(50vw - #{$width / 2});\r\n    padding-right: calc(50vw - #{$width / 2});\r\n  }\r\n}\r\n\r\n// Typographie\r\n// ==================\r\n@mixin font-face($name, $path, $weight: null, $style: null, $exts: eot woff2 woff ttf svg) {\r\n  $src: null;\r\n\r\n  $extmods: (\r\n    eot: \"?\",\r\n    svg: \"#\" + str-replace($name, \" \", \"_\")\r\n  );\r\n\r\n  $formats: (\r\n    otf: \"opentype\",\r\n    ttf: \"truetype\"\r\n  );\r\n\r\n  @each $ext in $exts {\r\n    $extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);\r\n    $format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);\r\n    $src: append($src, url(quote($path + \".\" + $extmod)) format(quote($format)), comma);\r\n  }\r\n\r\n  @if length($weight) == 1 {\r\n    @font-face {\r\n      font-family: quote($name);\r\n      font-style: $style;\r\n      font-weight: $weight;\r\n      src: $src;\r\n    }\r\n  } @else {\r\n    $weights: $weight;\r\n    @each $weight in $weights {\r\n      $suffix: map-get((\r\n        300: Light,\r\n        400: Regular,\r\n        500: Medium,\r\n        600: SemiBold,\r\n        700: Bold\r\n      ), $weight);\r\n      @include font-face($name, $path + \"-\" + $suffix,\r\n      $weight, $style, $exts)\r\n    }\r\n  }\r\n}\r\n","// Variables\r\n// ==================\r\n$font: 'Roboto', sans-serif;\r\n$font-alt: 'Montserrat', sans-serif;\r\n\r\n// Font face\r\n// ==================\r\n/*\r\n@include font-face('a', './fonts/HKGrotesk-Regular', 300 400 600 700, null, woff2 woff ttf svg eot);\r\n*/\r\n\r\n// Pour générer les polices : https://transfonter.org/","\r\n.form-field-style{\r\n    color: var(--colorActive);\r\n    border-bottom: 1px solid var(--colorTertiaire);\r\n    tr